import React from "react";
import { BrowserRouter as Router, Route } from "react-router-dom";
import "bootstrap/dist/css/bootstrap.min.css";

import Navbar from "./components/Navbar";
import ListMatrizFoda from "./components/ListMatrizFoda";
import CreateContentMatriz from "./components/CreateContentMatriz";
import CreateElementMatriz from "./components/CreateElementMatriz";
import MatrizFoda from "./components/MatrizFoda";
import GraficoFoda from "./components/GraficoFoda";
import CreateEstrategiaMatriz from "./components/CreateEstrategiaMatriz";
import GraficoEstrategiaFoda from "./components/GraficoEstrategiaFoda";
import ListEstrategiaFoda from "./components/ListEstrategiaFoda";

function App() {
  return (
    <Router>
      <Navbar />

      <Route exact path="/home" component={MatrizFoda} />
      <Route exact path="/listmatriz" component={ListMatrizFoda} />
      <Route exact path="/listestrategias" component={ListEstrategiaFoda} />
      <Route exact path="/editfoda/:id" component={CreateContentMatriz} />
      <Route
        exact
        path="/editestrategia/:id"
        component={CreateEstrategiaMatriz}
      />
      <Route exact path="/creatematriz" component={CreateContentMatriz} />
      <Route
        exact
        path="/createestrategia"
        component={CreateEstrategiaMatriz}
      />
      <Route exact path="/element" component={CreateElementMatriz} />
      <Route exact path="/graficofoda" component={GraficoFoda} />
      <Route
        exact
        path="/graficoestrategiafoda"
        component={GraficoEstrategiaFoda}
      />
    </Router>
  );
}

export default App;
