import React, { Component } from "react";
import "./styles/ListMatrizFoda.css";
import { Link } from "react-router-dom";
import axios from "axios";

export default class ListEstrategiaFoda extends Component {
  state = {
    resultEstrategiaMatriz: [],
  };

  componentDidMount() {
    this.getMatriz();
  }

  async getMatriz() {
    const res = await axios.get(
      "https://matrizdafo.herokuapp.com/api/estrategifoda"
    );
    this.setState({ resultEstrategiaMatriz: res.data });
  }

  deleteMatriz = async (id) => {
    await axios.delete(
      "https://matrizdafo.herokuapp.com/api/estrategifoda/" + id
    );
    this.getMatriz();
  };

  render() {
    return (
      <div className="container__list-matriz">
        <div className="row list__matriz">
          {this.state.resultEstrategiaMatriz.map((matriz) => (
            <div className=" list__matriz-content" key={matriz._id}>
              <div className="card card__content">
                <div className="card-header">
                  <h2 className="title__header">{matriz.nameEstrategia}</h2>
                </div>
                <div className="card-body">
                  <div className="card__list">
                    <img
                      width="24px"
                      src="https://www.flaticon.es/premium-icon/icons/svg/3423/3423653.svg"
                      alt=""
                    />
                    <p>{matriz.estrategia1}</p>
                  </div>
                  <div className="card__list">
                    <img
                      width="24px"
                      src="https://www.flaticon.es/premium-icon/icons/svg/3423/3423653.svg"
                      alt=""
                    />
                    <p>{matriz.estrategia2}</p>
                  </div>
                  <div className="card__list">
                    <img
                      width="24px"
                      src="https://www.flaticon.es/premium-icon/icons/svg/3423/3423653.svg"
                      alt=""
                    />
                    <p>{matriz.estrategia3}</p>
                  </div>
                  <div className="card__list">
                    <img
                      width="24px"
                      src="https://www.flaticon.es/premium-icon/icons/svg/3423/3423653.svg"
                      alt=""
                    />
                    <p>{matriz.estrategia4}</p>
                  </div>
                  <div className="card__list">
                    <img
                      width="24px"
                      src="https://www.flaticon.es/premium-icon/icons/svg/3423/3423653.svg"
                      alt=""
                    />
                    <p>{matriz.estrategia5}</p>
                  </div>
                  <div className="card__list">
                    <img
                      width="24px"
                      src="https://www.flaticon.es/premium-icon/icons/svg/3423/3423653.svg"
                      alt=""
                    />
                    <p>{matriz.estrategia6}</p>
                  </div>
                </div>

                <div className="card-footer d-flex justify-content-between">
                  <button
                    onClick={() => this.deleteMatriz(matriz._id)}
                    className="button__delete"
                  >
                    Delete
                  </button>

                  <Link
                    to={"/editestrategia/" + matriz._id}
                    className="button__edit"
                  >
                    Editar
                  </Link>
                </div>
              </div>
            </div>
          ))}
        </div>
        <div className="link__result">
          <Link className="link__action" to="graficoestrategiafoda">
            Generar Estrategias de la Matriz Foda
          </Link>
        </div>
      </div>
    );
  }
}
