import React, { Component } from "react";
import "./styles/MatrizFoda.css";

export default class MatrizFoda extends Component {
  render() {
    return (
      <div>
        <div className="strengths">
          <div className="siglas__matriz">
            <h1 className="f">F</h1>
          </div>
          <div className="strengths__content">
            <div className="img__strengths">
              <img
                width="100px"
                src="https://image.flaticon.com/icons/svg/2936/2936880.svg  "
                alt=""
              />
              <div>
                <span>FORTALEZAS</span>
              </div>
            </div>

            <div className="text__strengths">
              <p>
                También llamadas puntos fuertes. Son capacidades, recursos,
                posiciones alcanzadas y, consecuentemente, ventajas competitivas
                que deben y pueden servir para explotar oportunidades.
              </p>
            </div>
          </div>
        </div>

        <div className="strengths">
          <div className="siglas__matriz">
            <h1>O</h1>
          </div>
          <div className="strengths__content">
            <div className="img__strengths">
              <img
                width="100px"
                src="https://image.flaticon.com/icons/svg/1232/1232482.svg"
                alt=""
              />
              <div>
                <span>OPORTUNIDADES</span>
              </div>
            </div>

            <div className="text__strengths">
              <p>
                Se definen como toda fuerza del entorno que pueden ser
                aprovechadas como nuevos nichos de mercados o sectores con altos
                niveles de crecimiento que afectan positivamentea los objetivos
                de la empresa.
              </p>
            </div>
          </div>
        </div>

        <div className="strengths">
          <div className="siglas__matriz">
            <h1>D</h1>
          </div>
          <div className="strengths__content">
            <div className="img__strengths">
              <img
                width="100px"
                src="https://image.flaticon.com/icons/svg/667/667025.svg"
                alt=""
              />
              <div>
                <span>DEBILIDADES</span>
              </div>
            </div>

            <div className="text__strengths">
              <p>
                También llamadas puntos débiles. Son aspectos que limitan o
                reducen la capacidad de desarrollo efectivo de la estrategia de
                la empresa, constituyen una amenaza para la organización y
                deben, ser controladas y superadas.
              </p>
            </div>
          </div>
        </div>

        <div className="strengths">
          <div className="siglas__matriz">
            <h1>A</h1>
          </div>
          <div className="strengths__content">
            <div className="img__strengths">
              <img
                width="100px"
                src="https://image.flaticon.com/icons/svg/595/595067.svg"
                alt=""
              />
              <div>
                <span>AMENAZAS</span>
              </div>
            </div>

            <div className="text__strengths">
              <p>
                Se definen como toda fuerza del entorno que pueden impedir la
                implantación de una estrategia, o bien reducir su efectividad, o
                incrementar los riesgos de la misma, o los recursos que se
                requieren para su implantación, o bien reducir los ingresos
                operados o su rentabilidad.
              </p>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
