import React, { Component } from "react";
import axios from "axios";

export default class CreateElementMatriz extends Component {
  state = {
    elementMatriz: [],
    name: "",
  };

  async componentDidMount() {
    this.getElementMatriz();
    console.log(this.state.elementMatriz);
  }

  getElementMatriz = async () => {
    const res = await axios.get(
      "https://matrizdafo.herokuapp.com/api/elementosmatrizfoda"
    );
    this.setState({ elementMatriz: res.data });
  };

  onChangeElementName = (e) => {
    this.setState({
      name: e.target.value,
    });
  };

  onSubmit = async (e) => {
    e.preventDefault();
    await axios.post(
      "https://matrizdafo.herokuapp.com/api/elementosmatrizfoda",
      {
        name: this.state.name,
      }
    );
    this.setState({ name: "" });
    this.getElementMatriz();
  };

  deleteElement = async (id) => {
    await axios.delete(
      "https://matrizdafo.herokuapp.com/api/elementosmatrizfoda/" + id
    );
    this.getElementMatriz();
  };

  render() {
    return (
      <div className="container">
        <div className="matriz__content">
          <div className="matriz__form">
            <h2>Crea el Elemento de la Matriz FODA</h2>
            <form action="" onSubmit={this.onSubmit}>
              <input
                type="text"
                onChange={this.onChangeElementName}
                value={this.state.name}
              />
              <button type="submit">Agregar</button>
            </form>
          </div>
          <div>
            <ul>
              {this.state.elementMatriz.map((element) => (
                <li
                  className="h"
                  onDoubleClick={() => this.deleteElement(element._id)}
                  key={element._id}
                >
                  {element.name}
                </li>
              ))}
            </ul>
          </div>
        </div>
      </div>
    );
  }
}
