import React, { Component } from "react";
import "./styles/ListMatrizFoda.css";
import { Link } from "react-router-dom";
import axios from "axios";

export default class ListMatrizFoda extends Component {
  state = {
    resultMatriz: [],
  };

  componentDidMount() {
    this.getMatriz();
  }

  async getMatriz() {
    const res = await axios.get(
      "https://matrizdafo.herokuapp.com/api/resultfoda"
    );
    this.setState({ resultMatriz: res.data });
  }

  deleteMatriz = async (id) => {
    await axios.delete("https://matrizdafo.herokuapp.com/api/resultfoda/" + id);
    this.getMatriz();
  };

  render() {
    return (
      <div className="container__list-matriz">
        <div className="row list__matriz">
          {this.state.resultMatriz.map((matriz) => (
            <div className=" list__matriz-content" key={matriz._id}>
              <div className="card card__content">
                <div className="card-header">
                  <h2 className="title__header">{matriz.nameElement}</h2>
                </div>
                <div className="card-body">
                  <div className="card__list">
                    <img
                      width="24px"
                      src="https://www.flaticon.es/premium-icon/icons/svg/3423/3423653.svg"
                      alt=""
                    />

                    <p>{matriz.content1}</p>
                  </div>
                  <div className="card__list">
                    <img
                      width="24px"
                      src="https://www.flaticon.es/premium-icon/icons/svg/3423/3423653.svg"
                      alt=""
                    />

                    <p>{matriz.content2}</p>
                  </div>
                  <div className="card__list">
                    <img
                      width="24px"
                      src="https://www.flaticon.es/premium-icon/icons/svg/3423/3423653.svg"
                      alt=""
                    />

                    <p>{matriz.content3}</p>
                  </div>
                  <div className="card__list">
                    <img
                      width="24px"
                      src="https://www.flaticon.es/premium-icon/icons/svg/3423/3423653.svg"
                      alt=""
                    />

                    <p>{matriz.content4}</p>
                  </div>
                  <div className="card__list">
                    <img
                      width="24px"
                      src="https://www.flaticon.es/premium-icon/icons/svg/3423/3423653.svg"
                      alt=""
                    />

                    <p>{matriz.content5}</p>
                  </div>
                  <div className="card__list">
                    <img
                      width="24px"
                      src="https://www.flaticon.es/premium-icon/icons/svg/3423/3423653.svg"
                      alt=""
                    />

                    <p>{matriz.content6}</p>
                  </div>
                </div>

                <div className="card-footer d-flex justify-content-between">
                  <button
                    onClick={() => this.deleteMatriz(matriz._id)}
                    className="button__delete"
                  >
                    Delete
                  </button>

                  <Link to={"/editfoda/" + matriz._id} className="button__edit">
                    Editar
                  </Link>
                </div>
              </div>
            </div>
          ))}
        </div>
        <div className="link__result">
          <Link className="link__action" to="graficofoda">
            Generar Matriz Foda
          </Link>
        </div>
      </div>
    );
  }
}
