import React, { Component } from "react";
import "./styles/GraficoEstrategiaMatriz.css";
import axios from "axios";
import { Link } from "react-router-dom";

export default class GraficoEstrategiaFoda extends Component {
  state = {
    resultEstrategiaMatriz: [],
  };

  componentDidMount() {
    this.getMatriz();
  }

  async getMatriz() {
    const res = await axios.get(
      "https://matrizdafo.herokuapp.com/api/estrategifoda"
    );
    this.setState({ resultEstrategiaMatriz: res.data });
  }

  deleteMatriz = async (id) => {
    await axios.delete(
      "https://matrizdafo.herokuapp.com/api/estrategifoda/" + id
    );
    this.getMatriz();
  };

  render() {
    return (
      <div className="container__grafic">
        <div className="main__grafic-foda">
          <Link className="link__action" to="createestrategia">
            Regresar
          </Link>
          <h1 className="title__estrategi">ESTRATEGIAS MATRIZ FODA</h1>
          <div className="grid">
            <div className="blank1"></div>
            <div className="grid11">FORTALEZAS</div>
            <div className="grid22">DEBILIDADES</div>
            <div className="blank2"></div>
            <div className="grid33">OPORTUNIDADES</div>
            <div className="grid44">AMENAZAS</div>
            <div className="row grafic__foda-estrategia main">
              {this.state.resultEstrategiaMatriz.map((matriz) => (
                <div
                  className="grafic__matriz-content-estrategia"
                  key={matriz._id}
                >
                  <div className="card grafic__content">
                    <div className="card-header">
                      <h2 className="title__header">{matriz.nameEstrategia}</h2>
                    </div>
                    <div className="card-body">
                      <div className="card__list">
                        <img
                          width="24px"
                          src="https://www.flaticon.es/premium-icon/icons/svg/3423/3423653.svg"
                          alt=""
                        />

                        <p>{matriz.estrategia1}</p>
                      </div>
                      <div className="card__list">
                        <img
                          width="24px"
                          src="https://www.flaticon.es/premium-icon/icons/svg/3423/3423653.svg"
                          alt=""
                        />

                        <p>{matriz.estrategia2}</p>
                      </div>
                      <div className="card__list">
                        <img
                          width="24px"
                          src="https://www.flaticon.es/premium-icon/icons/svg/3423/3423653.svg"
                          alt=""
                        />

                        <p>{matriz.estrategia3}</p>
                      </div>
                      <div className="card__list">
                        <img
                          width="24px"
                          src="https://www.flaticon.es/premium-icon/icons/svg/3423/3423653.svg"
                          alt=""
                        />

                        <p>{matriz.estrategia4}</p>
                      </div>
                      <div className="card__list">
                        <img
                          width="24px"
                          src="https://www.flaticon.es/premium-icon/icons/svg/3423/3423653.svg"
                          alt=""
                        />

                        <p>{matriz.estrategia5}</p>
                      </div>
                      <div className="card__list">
                        <img
                          width="24px"
                          src="https://www.flaticon.es/premium-icon/icons/svg/3423/3423653.svg"
                          alt=""
                        />

                        <p>{matriz.estrategia6}</p>
                      </div>
                    </div>
                  </div>
                </div>
              ))}
            </div>
          </div>
        </div>
      </div>
    );
  }
}
