import React, { Component } from "react";
import "./styles/CreateMatriz.css";
import axios from "axios";
import ListMatrizFoda from "./ListMatrizFoda";

export default class CreateContentMatriz extends Component {
  state = {
    elementMatriz: [],
    elementSelectd: "",
    content1: "",
    content2: "",
    content3: "",
    content4: "",
    content5: "",
    content6: "",
    editing: false,
    _id: "",
  };

  async componentDidMount() {
    const res = await axios.get(
      "https://matrizdafo.herokuapp.com/api/elementosmatrizfoda"
    );
    this.setState({
      elementMatriz: res.data.map((element) => element.name),
      elementSelectd: res.data[0].name,
    });

    if (this.props.match.params.id) {
      const res = await axios.get(
        "https://matrizdafo.herokuapp.com/api/resultfoda/" +
          this.props.match.params.id
      );
      this.setState({
        elementSelectd: res.data.nameElement,
        content1: res.data.content1,
        content2: res.data.content2,
        content3: res.data.content3,
        content4: res.data.content4,
        content5: res.data.content5,
        content6: res.data.content6,
        editing: true,
        _id: this.props.match.params.id,
      });
    }
  }

  onSubmit = async (e) => {
    e.preventDefault();

    const newContentFoda = {
      nameElement: this.state.elementSelectd,
      content1: this.state.content1,
      content2: this.state.content2,
      content3: this.state.content3,
      content4: this.state.content4,
      content5: this.state.content5,
      content6: this.state.content6,
    };

    if (this.state.editing) {
      await axios.put(
        "https://matrizdafo.herokuapp.com/api/resultfoda/" + this.state._id,
        newContentFoda
      );
    } else {
      await axios.post(
        "https://matrizdafo.herokuapp.com/api/resultfoda",
        newContentFoda
      );
    }

    window.location.href = "/creatematriz";
  };

  onInputChange = (e) => {
    this.setState({
      [e.target.name]: e.target.value,
    });
  };

  render() {
    return (
      <div className="fill__matriz">
        <div className="col-md-6 offset-md-3 widt__form">
          <div className="card card-body card__form">
            <h2 className="title__form">Rellenar Matriz</h2>

            {}

            <div className="form-group">
              <select
                className="form-control"
                name="elementSelectd"
                onChange={this.onInputChange}
                value={this.state.elementSelectd}
              >
                {this.state.elementMatriz.map((element) => (
                  <option key={element} value={element}>
                    {element}
                  </option>
                ))}
              </select>
            </div>

            <div className="line__separed"></div>

            <div className="list__input">
              <div className="form-group">
                <div className="check-list">
                  <img
                    width="24px"
                    src="https://image.flaticon.com/icons/svg/1828/1828717.svg"
                    alt=""
                  />
                </div>
                <input
                  type="text"
                  className="form-control"
                  placeholder=""
                  name="content1"
                  onChange={this.onInputChange}
                  value={this.state.content1}
                />
              </div>

              <div className="form-group">
                <div className="check-list">
                  <img
                    width="24px"
                    src="https://image.flaticon.com/icons/svg/1828/1828717.svg"
                    alt=""
                  />
                </div>
                <input
                  type="text"
                  className="form-control"
                  placeholder=""
                  name="content2"
                  onChange={this.onInputChange}
                  value={this.state.content2}
                />
              </div>

              <div className="form-group">
                <div className="check-list">
                  <img
                    width="24px"
                    src="https://image.flaticon.com/icons/svg/1828/1828717.svg"
                    alt=""
                  />
                </div>
                <input
                  type="text"
                  className="form-control"
                  placeholder=""
                  name="content3"
                  onChange={this.onInputChange}
                  value={this.state.content3}
                />
              </div>

              <div className="form-group">
                <div className="check-list">
                  <img
                    width="24px"
                    src="https://image.flaticon.com/icons/svg/1828/1828717.svg"
                    alt=""
                  />
                </div>
                <input
                  type="text"
                  className="form-control"
                  placeholder=""
                  name="content4"
                  onChange={this.onInputChange}
                  value={this.state.content4}
                />
              </div>

              <div className="form-group">
                <div className="check-list">
                  <img
                    width="24px"
                    src="https://image.flaticon.com/icons/svg/1828/1828717.svg"
                    alt=""
                  />
                </div>
                <input
                  type="text"
                  className="form-control"
                  placeholder=""
                  name="content5"
                  onChange={this.onInputChange}
                  value={this.state.content5}
                />
              </div>

              <div className="form-group">
                <div className="check-list">
                  <img
                    width="24px"
                    src="https://image.flaticon.com/icons/svg/1828/1828717.svg"
                    alt=""
                  />
                </div>
                <input
                  type="text"
                  className="form-control"
                  placeholder=""
                  name="content6"
                  onChange={this.onInputChange}
                  value={this.state.content6}
                />
              </div>
            </div>
            <form onSubmit={this.onSubmit}>
              <button className="button__save" type="submit">
                Guardar
              </button>
            </form>
          </div>
        </div>

        <ListMatrizFoda />
      </div>
    );
  }
}
