import React, { Component } from "react";
import "./styles/GraficoFoda.css";
import axios from "axios";
import { Link } from "react-router-dom";

export default class GraficoFoda extends Component {
  state = {
    resultMatriz: [],
  };

  componentDidMount() {
    this.getMatriz();
  }

  async getMatriz() {
    const res = await axios.get(
      "https://matrizdafo.herokuapp.com/api/resultfoda"
    );
    this.setState({ resultMatriz: res.data });
  }

  deleteMatriz = async (id) => {
    await axios.delete("https://matrizdafo.herokuapp.com/api/resultfoda/" + id);
    this.getMatriz();
  };

  render() {
    return (
      <div className="container__grafic">
        <div className="main__grafic-foda">
          <Link className="link__action" to="creatematriz">
            Regresar
          </Link>
          <h1 className="title__estrategi">MATRIZ FODA</h1>
          <div className="grid">
            <div className="blank"></div>
            <div className="grid1">Analisis Interno</div>
            <div className="grid2">Analisis Externo</div>
            <div className="blank"></div>
            <div className="grid3">Aspecto Negativo</div>
            <div className="grid4">Aspecto Postivo</div>
            <div className="row grafic__foda main">
              {this.state.resultMatriz.map((matriz) => (
                <div className="grafic__matriz-content" key={matriz._id}>
                  <div className="card grafic__content">
                    <div className="card-header">
                      <h2 className="title__header">{matriz.nameElement}</h2>
                    </div>
                    <div className="card-body">
                      <div className="card__list">
                        <img
                          width="24px"
                          src="https://www.flaticon.es/premium-icon/icons/svg/3423/3423653.svg"
                          alt=""
                        />

                        <p>{matriz.content1}</p>
                      </div>
                      <div className="card__list">
                        <img
                          width="24px"
                          src="https://www.flaticon.es/premium-icon/icons/svg/3423/3423653.svg"
                          alt=""
                        />

                        <p>{matriz.content2}</p>
                      </div>
                      <div className="card__list">
                        <img
                          width="24px"
                          src="https://www.flaticon.es/premium-icon/icons/svg/3423/3423653.svg"
                          alt=""
                        />

                        <p>{matriz.content3}</p>
                      </div>
                      <div className="card__list">
                        <img
                          width="24px"
                          src="https://www.flaticon.es/premium-icon/icons/svg/3423/3423653.svg"
                          alt=""
                        />

                        <p>{matriz.content4}</p>
                      </div>
                      <div className="card__list">
                        <img
                          width="24px"
                          src="https://www.flaticon.es/premium-icon/icons/svg/3423/3423653.svg"
                          alt=""
                        />

                        <p>{matriz.content5}</p>
                      </div>
                      <div className="card__list">
                        <img
                          width="24px"
                          src="https://www.flaticon.es/premium-icon/icons/svg/3423/3423653.svg"
                          alt=""
                        />

                        <p>{matriz.content6}</p>
                      </div>
                    </div>
                  </div>
                </div>
              ))}
            </div>
          </div>
        </div>
      </div>
    );
  }
}
