import React, { Component } from "react";
import "./styles/CreateMatriz.css";
import axios from "axios";
import ListEstrategiaFoda from "./ListEstrategiaFoda";

export default class CreateEstrategiaMatriz extends Component {
  state = {
    estrategiaMatriz: [],
    estrategiaSelectd: "",
    estrategia1: "",
    estrategia2: "",
    estrategia3: "",
    estrategia4: "",
    estrategia5: "",
    estrategia6: "",
    editing: false,
    _id: "",
  };

  async componentDidMount() {
    const res = await axios.get(
      "https://matrizdafo.herokuapp.com/api/tipoestrategifoda"
    );
    this.setState({
      estrategiaMatriz: res.data.map((element) => element.nameTipoEstrategia),
      estrategiaSelectd: res.data[0].nameTipoEstrategia,
    });

    if (this.props.match.params.id) {
      const res = await axios.get(
        "https://matrizdafo.herokuapp.com/api/estrategifoda/" +
          this.props.match.params.id
      );
      this.setState({
        estrategiaSelectd: res.data.nameEstrategia,
        estrategia1: res.data.estrategia1,
        estrategia2: res.data.estrategia2,
        estrategia3: res.data.estrategia3,
        estrategia4: res.data.estrategia4,
        estrategia5: res.data.estrategia5,
        estrategia6: res.data.estrategia6,
        editing: true,
        _id: this.props.match.params.id,
      });
    }
  }

  onSubmit = async (e) => {
    e.preventDefault();

    const newEstrategiaFoda = {
      nameEstrategia: this.state.estrategiaSelectd,
      estrategia1: this.state.estrategia1,
      estrategia2: this.state.estrategia2,
      estrategia3: this.state.estrategia3,
      estrategia4: this.state.estrategia4,
      estrategia5: this.state.estrategia5,
      estrategia6: this.state.estrategia6,
    };

    if (this.state.editing) {
      await axios.put(
        "https://matrizdafo.herokuapp.com/api/estrategifoda/" + this.state._id,
        newEstrategiaFoda
      );
    } else {
      await axios.post(
        "https://matrizdafo.herokuapp.com/api/estrategifoda",
        newEstrategiaFoda
      );
    }

    window.location.href = "/createestrategia";
  };

  onInputChange = (e) => {
    this.setState({
      [e.target.name]: e.target.value,
    });
  };

  render() {
    return (
      <div className="fill__matriz">
        <div className="col-md-6 offset-md-3 widt__form">
          <div className="card card-body card__form">
            <h2 className="title__form">Rellenar Estrategias</h2>

            {}

            <div className="form-group">
              <select
                className="form-control"
                name="estrategiaSelectd"
                onChange={this.onInputChange}
                value={this.state.estrategiaSelectd}
              >
                {this.state.estrategiaMatriz.map((element) => (
                  <option key={element} value={element}>
                    {element}
                  </option>
                ))}
              </select>
            </div>

            <div className="line__separed"></div>

            <div className="list__input">
              <div className="form-group">
                <div className="check-list">
                  <img
                    width="24px"
                    src="https://image.flaticon.com/icons/svg/1828/1828717.svg"
                    alt=""
                  />
                </div>
                <input
                  type="text"
                  className="form-control"
                  placeholder=""
                  name="estrategia1"
                  onChange={this.onInputChange}
                  value={this.state.estrategia1}
                />
              </div>

              <div className="form-group">
                <div className="check-list">
                  <img
                    width="24px"
                    src="https://image.flaticon.com/icons/svg/1828/1828717.svg"
                    alt=""
                  />
                </div>
                <input
                  type="text"
                  className="form-control"
                  placeholder=""
                  name="estrategia2"
                  onChange={this.onInputChange}
                  value={this.state.estrategia2}
                />
              </div>

              <div className="form-group">
                <div className="check-list">
                  <img
                    width="24px"
                    src="https://image.flaticon.com/icons/svg/1828/1828717.svg"
                    alt=""
                  />
                </div>
                <input
                  type="text"
                  className="form-control"
                  placeholder=""
                  name="estrategia3"
                  onChange={this.onInputChange}
                  value={this.state.estrategia3}
                />
              </div>

              <div className="form-group">
                <div className="check-list">
                  <img
                    width="24px"
                    src="https://image.flaticon.com/icons/svg/1828/1828717.svg"
                    alt=""
                  />
                </div>
                <input
                  type="text"
                  className="form-control"
                  placeholder=""
                  name="estrategia4"
                  onChange={this.onInputChange}
                  value={this.state.estrategia4}
                />
              </div>

              <div className="form-group">
                <div className="check-list">
                  <img
                    width="24px"
                    src="https://image.flaticon.com/icons/svg/1828/1828717.svg"
                    alt=""
                  />
                </div>
                <input
                  type="text"
                  className="form-control"
                  placeholder=""
                  name="estrategia5"
                  onChange={this.onInputChange}
                  value={this.state.estrategia5}
                />
              </div>

              <div className="form-group">
                <div className="check-list">
                  <img
                    width="24px"
                    src="https://image.flaticon.com/icons/svg/1828/1828717.svg"
                    alt=""
                  />
                </div>
                <input
                  type="text"
                  className="form-control"
                  placeholder=""
                  name="estrategia6"
                  onChange={this.onInputChange}
                  value={this.state.estrategia6}
                />
              </div>
            </div>

            <form onSubmit={this.onSubmit}>
              <button className="button__save" type="submit">
                Guardar
              </button>
            </form>
          </div>
        </div>

        <ListEstrategiaFoda />
      </div>
    );
  }
}
